.schedule .day {
  min-width: 240px;
  padding: 0;
  margin: 0;
  max-width: unset;
}

.schedule .day .container.double {
  padding: 0 1.25rem !important;
}

.schedule .day .container .row {
  align-items: center !important;
}

.schedule .day .container.double .row .col-4 {
  padding: 0.75rem 15px !important;
}

.schedule .desktop .day.today {
  box-shadow: 0 0 50px -5px #2685c7;
}

.schedule .desktop .day.today .now-lesson {
  background-color: #e1f3ff;
}

.schedule .slick-slider .day.today {
  box-shadow: 0 0 25px -5px #2685c7;
}