.header {
  padding: 0.8rem 0;
  margin-bottom: 1rem;
}
.header .now h4 {
  text-transform: capitalize;
}
.filter {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.filter .list-group-item {
  padding: 0.75rem 0;
}

@media (max-width: 576px) {
  .filter {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
}

.filter .list-group {
  margin-bottom: 20px;
}

.filter .s-table, .filter .facs {
  -webkit-flex-direction: unset;
          flex-direction: unset;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;  
}

.filter .s-table .list-group-item {
  width: 50%;
  margin-right: -1px;
}

.filter .s-table .list-group-item:nth-child(1) {
  border-top-left-radius: 0.25rem;
  border-top-right-radius: unset;
}

.filter .s-table .list-group-item:nth-child(2) {
  border-top-right-radius: 0.25rem;
  border-top-left-radius: unset;
}

.filter .s-table .list-group-item:nth-last-child(1):nth-child(even) {
  margin-bottom: 0;
  border-bottom-right-radius: 0.25rem;
  border-bottom-left-radius: unset;
}


.filter .s-table .list-group-item:nth-last-child(2):nth-child(odd) {
  margin-bottom: 0;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: unset;
}

.filter .s-table .list-group-item:nth-last-child(2):nth-child(even) {
  border-bottom-right-radius: 0.25rem;
  border-bottom-left-radius: unset;
}

.filter .groups {
  max-height: 18rem;
  overflow-y: auto;
  position: relative;
}

.filter .groups::-webkit-scrollbar {
  display: none;
}
.schedule .day {
  min-width: 240px;
  padding: 0;
  margin: 0;
  max-width: unset;
}

.schedule .day .container.double {
  padding: 0 1.25rem !important;
}

.schedule .day .container .row {
  -webkit-align-items: center !important;
          align-items: center !important;
}

.schedule .day .container.double .row .col-4 {
  padding: 0.75rem 15px !important;
}

.schedule .desktop .day.today {
  box-shadow: 0 0 50px -5px #2685c7;
}

.schedule .desktop .day.today .now-lesson {
  background-color: #e1f3ff;
}

.schedule .slick-slider .day.today {
  box-shadow: 0 0 25px -5px #2685c7;
}
