.filter {
  user-select: none;
}

.filter .list-group-item {
  padding: 0.75rem 0;
}

@media (max-width: 576px) {
  .filter {
    display: flex;
    flex-wrap: wrap;
  }
}

.filter .list-group {
  margin-bottom: 20px;
}

.filter .s-table, .filter .facs {
  flex-direction: unset;
  flex-wrap: wrap;  
}

.filter .s-table .list-group-item {
  width: 50%;
  margin-right: -1px;
}

.filter .s-table .list-group-item:nth-child(1) {
  border-top-left-radius: 0.25rem;
  border-top-right-radius: unset;
}

.filter .s-table .list-group-item:nth-child(2) {
  border-top-right-radius: 0.25rem;
  border-top-left-radius: unset;
}

.filter .s-table .list-group-item:nth-last-child(1):nth-child(even) {
  margin-bottom: 0;
  border-bottom-right-radius: 0.25rem;
  border-bottom-left-radius: unset;
}


.filter .s-table .list-group-item:nth-last-child(2):nth-child(odd) {
  margin-bottom: 0;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: unset;
}

.filter .s-table .list-group-item:nth-last-child(2):nth-child(even) {
  border-bottom-right-radius: 0.25rem;
  border-bottom-left-radius: unset;
}

.filter .groups {
  max-height: 18rem;
  overflow-y: auto;
  position: relative;
}

.filter .groups::-webkit-scrollbar {
  display: none;
}